<template>
  <div>
    <app-header href="/" title="Posts" />
    <div class="center-align" v-if="busy">
      <div class="preloader-wrapper big active">
        <div class="spinner-layer spinner-blue">
          <div class="circle-clipper left">
            <div class="circle"></div>
          </div>
          <div class="gap-patch">
            <div class="circle"></div>
          </div>
          <div class="circle-clipper right">
            <div class="circle"></div>
          </div>
        </div>

        <div class="spinner-layer spinner-red">
          <div class="circle-clipper left">
            <div class="circle"></div>
          </div>
          <div class="gap-patch">
            <div class="circle"></div>
          </div>
          <div class="circle-clipper right">
            <div class="circle"></div>
          </div>
        </div>

        <div class="spinner-layer spinner-yellow">
          <div class="circle-clipper left">
            <div class="circle"></div>
          </div>
          <div class="gap-patch">
            <div class="circle"></div>
          </div>
          <div class="circle-clipper right">
            <div class="circle"></div>
          </div>
        </div>

        <div class="spinner-layer spinner-green">
          <div class="circle-clipper left">
            <div class="circle"></div>
          </div>
          <div class="gap-patch">
            <div class="circle"></div>
          </div>
          <div class="circle-clipper right">
            <div class="circle"></div>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="card-panel hoverable">
      <div class="row">
        <div class="truncate col s9">
          <router-link :to="{ name: 'Post', params: { postId: post.id }}">{{ post.title }}</router-link>
        </div>
        <div class="truncate col s3 right-align">Опубликовано: {{ post.createdAtLocale }}</div>
      </div>
      <div class="flow-text" v-html="post.content">
      </div>
      <div class="right-align">
        <div class="chip" v-for="tag in post.tags">
          <router-link :to="{ name: 'Tag', params: { tagId: tag }}">{{ tag }}</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import firebase from 'firebase'
  import AppHeader from './AppHeader'

  function getLang () {
    if (navigator.languages !== undefined) {
      return navigator.languages[0]
    } else {
      return navigator.language
    }
  }

  export default {
    components: {AppHeader},
    name: 'post',
    data: function () {
      return {
        post: {
          id: '',
          title: '',
          content: '',
          createdAtLocale: '',
          tags: []
        },
        busy: true
      }
    },
    mounted: function () {
      firebase.firestore().collection('posts').doc(this.$route.params.postId).get().then((doc) => {
        if (doc.exists) {
          this.post.id = doc.id
          this.post.title = doc.get('title')
          this.post.content = doc.get('content')
          this.post.createdAtLocale = (new Date(doc.get('createdAt'))).toLocaleString(getLang(), {
            year: 'numeric',
            month: 'numeric',
            day: 'numeric',
            timezone: 'UTC',
            hour: 'numeric',
            minute: 'numeric'
          })
          this.post.tags = doc.get('tags')
          this.busy = false
        } else {
          this.$router.push({
            name: 'PageNotFound'
          })
        }
      })
    }
  }
</script>

<style scoped>

</style>



