<template>
  <div id="#header"><h1 class="center-align"><a :href="href">{{ title }}</a></h1></div>
</template>

<script>
  export default {
    name: 'app-header',
    props: {
      title: {
        type: String,
        required: true
      },
      href: {
        type: String,
        required: true
      }
    }
  }
</script>

<style scoped>

</style>



