<template>
  <div>
    <app-header href="/" title="Posts" />
    <div class="card-panel hoverable">
      <div class="row">
        <div class="input-field col s12">
          <input id="title" type="text" class="validate" v-model="post.title" v-validate="'required|min:3'"
                 name="title"
                 v-bind:class="{ invalid: errors.has('title')}">
          <label for="title" v-if="errors.has('title')">{{ errors.first('title') }}</label>
          <label for="title" v-else>Заголовок:</label>

        </div>
        <div class="input-field col s12" v-if="!previewContent">
            <textarea id="content" class="materialize-textarea" v-model="post.content" v-validate="'required|min:3'"
                      name="content"
                      :class="{ invalid: errors.has('content')}"></textarea>
          <label for="content" v-if="errors.has('content')">{{ errors.first('content') }}</label>
          <label for="content" v-else>Текст:</label>
        </div>
        <div class="flow-text" v-else v-html="post.content">
        </div>
        <div class="col s12 right-align">
          <button class="waves-effect waves-light btn-flat" @click="previewContent = !previewContent">{{ previewContent
            ? 'Редактировать' : 'Предпросмотр'}}
          </button>
        </div>
        <div class="input-field col s12" v-if="!previewDescription">
            <textarea id="description" class="materialize-textarea" data-length="3000" v-model="post.description"
                      v-validate="'min:3|max:3000'"
                      name="description"
                      v-bind:class="{ invalid: errors.has('description')}"></textarea>
          <label for="description" v-if="errors.has('description')">{{ errors.first('description') }}</label>
          <label for="description" v-else>Описание:</label>
        </div>
        <div class="flow-text" v-else v-html="post.description">
        </div>
        <div class="col s12 right-align">
          <button class="waves-effect waves-light btn-flat" @click="previewDescription = !previewDescription">{{
            previewDescription ? 'Редактировать' : 'Предпросмотр'}}
          </button>
        </div>
      </div>
      <div class="chips chips-placeholder">
      </div>
      <div class="right-align">
        <button class="waves-effect waves-light btn" @click="newPost">Опубликовать</button>
      </div>
    </div>
  </div>
</template>

<script>
  import firebase from 'firebase'
  import $ from 'jquery'
  import Materialize from 'materialize-css'
  import AppHeader from './AppHeader'

  export default {
    components: {AppHeader},
    name: 'new-post',
    data: function () {
      return {
        post: {
          title: '',
          description: '',
          content: '',
          tags: []
        },
        previewContent: false,
        previewDescription: false
      }
    },
    methods: {
      newPost: function () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            firebase.firestore().collection('posts').add({
              title: this.post.title,
              description: this.post.description,
              content: this.post.content,
              tags: this.post.tags
            }).then(function (docRef) {
              console.log('Document written with ID: ', docRef.id)
              let postId = docRef.id
              this.$router.push({
                name: 'Post',
                params: {
                  postId: postId
                }
              })
            })
              .catch(function (error) {
                console.error('Error adding document: ', error)
              })
          }
        })
      }
    },
    mounted: function () {
      let self = this
      Materialize.updateTextFields()
      $('#content').trigger('autoresize')
      let chips = $('.chips')
      chips.material_chip({
        placeholder: 'Enter a tag',
        secondaryPlaceholder: '+Tag'
      })
      chips.on('chip.add', function (e, chip) {
        self.post.tags.push(chip.tag)
      })
      chips.on('chip.delete', function (el, chip) {
        let index = self.post.tags.indexOf(chip.tag)
        if (index) {
          self.post.tags.splice(index, 1)
        }
      })
    }
  }
</script>

<style scoped>
</style>



