<template>
  <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy"
       infinite-scroll-distance="10">
    <app-header href="/" title="Posts" />
    <div class="card-panel hoverable" v-for="post in posts">
      <div class="row">
        <div class="truncate col s9">
          <router-link :to="{ name: 'Post', params: { postId: post.id }}">{{ post.title }}</router-link>
        </div>
        <div class="truncate col s3 right-align">Опубликовано: {{ post.createdAtLocale }}</div>
      </div>
      <div class="flow-text" v-html="post.description">
      </div>
      <div class="right-align">
        <div class="chip" v-for="tag in post.tags">
          <router-link :to="{ name: 'Tag', params: { tagId: tag }}">{{ tag }}</router-link>
        </div>
      </div>
      <div>
        <router-link :to="{ name: 'Post', params: { postId: post.id }}" class="waves-effect waves-light btn">Читать
          дальше
        </router-link>
      </div>
    </div>
    <div class="center-align" v-if="busy && posts.length !== totalPosts">
      <div class="preloader-wrapper big active">
        <div class="spinner-layer spinner-blue">
          <div class="circle-clipper left">
            <div class="circle"></div>
          </div>
          <div class="gap-patch">
            <div class="circle"></div>
          </div>
          <div class="circle-clipper right">
            <div class="circle"></div>
          </div>
        </div>

        <div class="spinner-layer spinner-red">
          <div class="circle-clipper left">
            <div class="circle"></div>
          </div>
          <div class="gap-patch">
            <div class="circle"></div>
          </div>
          <div class="circle-clipper right">
            <div class="circle"></div>
          </div>
        </div>

        <div class="spinner-layer spinner-yellow">
          <div class="circle-clipper left">
            <div class="circle"></div>
          </div>
          <div class="gap-patch">
            <div class="circle"></div>
          </div>
          <div class="circle-clipper right">
            <div class="circle"></div>
          </div>
        </div>

        <div class="spinner-layer spinner-green">
          <div class="circle-clipper left">
            <div class="circle"></div>
          </div>
          <div class="gap-patch">
            <div class="circle"></div>
          </div>
          <div class="circle-clipper right">
            <div class="circle"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import firebase from 'firebase'
  import AppHeader from './AppHeader'

  function getLang () {
    if (navigator.languages !== undefined) {
      return navigator.languages[0]
    } else {
      return navigator.language
    }
  }

  let postsRef

  export default {
    components: {AppHeader},
    name: 'index',
    data: function () {
      return {
        posts: [],
        busy: false,
        perPage: 2,
        totalPosts: 1,
        currentPage: 1,
        last_content: false
      }
    },
    // computed: {},
    created: function () {
      postsRef = firebase.firestore().collection('posts').orderBy('createdAt', 'desc').limit(2)
      firebase.firestore().collection('posts').doc('meta').get().then((meta) => {
        this.totalPosts = meta.get('total')
      })
      // firebase.database().ref('posts').orderByChild('createdAt').once('value', this.postsCallback)
    },
    methods: {
      fetchPage: function () {
        console.log('[start] fetchPage:' + new Date())
        postsRef.get().then((snapshot) => {
          let last = snapshot.docs[snapshot.docs.length - 1]
          postsRef = firebase.firestore().collection('posts').orderBy('createdAt', 'desc').startAfter(last).limit(2)
          let tmp = []
          snapshot.forEach((post) => {
            tmp.push({
              id: post.id,
              title: post.get('title'),
              description: post.get('description'),
              createdAt: post.get('createdAt'),
              createdAtLocale: (new Date(post.get('createdAt'))).toLocaleString(getLang(), {
                year: 'numeric',
                month: 'numeric',
                day: 'numeric',
                timezone: 'UTC',
                hour: 'numeric',
                minute: 'numeric'
              }),
              tags: post.get('tags') || []
            })
          })
          // tmp = tmp.reverse()
          // this.posts = tmp
          tmp.forEach((post) => {
            this.posts.push(post)
          })
          this.busy = false
          console.log('[end] fetchPage:' + new Date())
        })
      },
      loadMore: function () {
        if (!this.busy) {
          this.busy = true
          if (this.posts.length !== this.totalPosts) {
            this.fetchPage()
          }
        }
      }
    }
  }
</script>

<style>
</style>



